import { useEffect, useState } from "react";
import "./App.css";
import avatar from "./avatar.png";

//components
import Projects from "./components/Projects";
import Home from "./components/Home";
import Skills from "./components/Skills";
import Contact from "./components/Contact";

const App = () => {
  const darktheme = [
    {
      backgroundColor: "#242B2E",
      color: "white",
    },
    {
      backgroundColor: "#0D0D0D",
      boxShadow: "-1px 4px 20px -6px white",
    },
  ];
  const lightTheme = [
    {
      backgroundColor: "white",
    },
    {},
  ];
  const [darkMode, setDarkMode] = useState(false);
  const [mainWindow, setMainWindow] = useState(<Home />);
  const autoDark = () => {
    let date = new Date();
    let hrs = date.getHours();
    if (hrs < 7 || hrs > 18) {
      setDarkMode(true);
    }
  };

  useEffect(() => {
    autoDark();
  }, []);
  return (
    <div className="App" style={darkMode ? darktheme[0] : lightTheme[0]}>
      <div className="avatar">
        <button
          onClick={() => {
            setMainWindow(<Home />);
          }}
        >
          <img src={avatar} width={100} alt="Scyther" />
        </button>
        <label className="switch">
          {darkMode ? (
            <input
              type="checkbox"
              id="checkbox"
              checked
              onClick={() => {
                setDarkMode(!darkMode);
              }}
            />
          ) : (
            <input
              type="checkbox"
              id="checkbox"
              onClick={() => {
                setDarkMode(!darkMode);
              }}
            />
          )}
          <div className="slider round"></div>
        </label>
      </div>

      <div className="nav">
        <button
          onClick={() => {
            setMainWindow(<Projects />);
          }}
        >
          Projects
        </button>
        <button
          onClick={() => {
            setMainWindow(<Skills />);
          }}
        >
          Skills
        </button>
        <button
          onClick={() => {
            setMainWindow(<Contact />);
          }}
        >
          Contact
        </button>
      </div>
      <div className="main" style={darkMode ? darktheme[1] : lightTheme[1]}>
        {mainWindow}
      </div>
      <div className="footer">
        <p className="footer_text">Made with ❤️ by Chirag Sharma</p>
      </div>
    </div>
  );
};

export default App;
